<template id="home">
  <div class="login-all">
    <div class="login clearfix">
      <div class="login-left">
        <img src="../assets/img/login/icon.png" alt="" class="login-icon">
        <h1></h1>
        <h2></h2>
        <h3>WELCOME</h3>
      </div>
      <div class="login-right">
        <div class="login-right-box">
          <h2></h2>
          <form class="login-form">
            <div class="login-title clearfix">
              <a href="javascript:void(0)"></a>
            </div>
            <div class="login-main">
              <div class="login-item">
                <img src="../assets/img/login/zh.png" alt="">
                <span></span>
                <input type="text" name="" value="" v-model="account">
              </div>
              <div class="login-item">
                <img src="../assets/img/login/password.png" alt="">
                <span></span>
                <input type="password" name="" value="" v-model="password" @keyup.enter="login">
              </div>
              <div class="login-btn" @click="login()"></div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import Vue from "vue";
import axios from 'axios'
import storage from 'good-storage'
import md5 from 'js-md5';
var that= {};
export default {
   data(){
     return{
       account:'',
       password:''
     }
   },
   computed:{

   },
   beforeCreate(){
        that = this;
    },
    created(){

    },

    mounted:function(){
      that.getRem(1920, 100);
    },
    methods:{
      /**/
      error (msg) {
        this.$Notice.error({
          title: '',
          desc: msg
        })
      },
      /**/
      login(){
        if(that.account==''){
          that.error('')
        }else if(that.password==''){
          that.error("")
        }else{
          that.$post(
            that.global.url+'/manage/login',
              {
                account:that.account,
                password:that.password
              }
          ).then((response) => {
            console.log(response)
            if(response.code==200){
              storage.set("token",response.data.token);
              storage.set("userInfo",response.data);
              that.$router.push({path: '/home'});
              // next({path:'/home'})
            }else if(response.code==300){
              that.error(response.message)
            }else{
              that.error('')
            }
          })
        }

      }
    },
    components:{

    }
  }

</script>

<style src="../assets/css/common.scss" lang="scss"></style>
<style src="../assets/css/login.scss" lang="scss"></style>



