import Vue from 'vue'
import Router from 'vue-router'
import storage from 'good-storage'
import home from '../views/home.vue'
import login from '../views/login.vue'
import monitoring from '../views/monitoring.vue'
import bedManage from '../views/bedManage.vue'
import deviceManage from '../views/deviceManage.vue'
import accountManage from '../views/accountManage.vue'
import history from '../views/history.vue'
import routerManage from '../views/routerManage.vue'


Vue.use(Router)

const router =  new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'login',
      component:login ,
    },
    {
      path: '/home',
      name: 'home',
      component: home,
    },
    {
      path: '/monitoring',
      name: 'monitoring',
      component: monitoring,
    },
    {
      path: '/bedManage',
      name: 'bedManage',
      component: bedManage,
    },
    {
      path: '/deviceManage',
      name: 'deviceManage',
      component: deviceManage,
    },
    {
      path: '/accountManage',
      name: 'accountManage',
      component: accountManage,
    },
    {
      path: '/history',
      name: 'history',
      component: history,
    },
    {
      path: '/routerManage',
      name: 'routerManage',
      component: routerManage,
    }
  ]
})


/*  */
router.beforeEach((to, from, next) => {

  const nextRoute = ['accountManage', 'bedManage', 'deviceManage','routerManage','home','history','monitoring'];
  const nextRoute1 = ['accountManage', 'bedManage', 'deviceManage','routerManage'];
  let canGo;
  /**/
  console.log(storage.get("userInfo"))
  console.log(to.path)
  console.log(Boolean(storage.get("userInfo")))
  if(Boolean(storage.get("userInfo"))){/**/
    if(to.path === '/login'){/*-->home*/
      next({path:'/home'})
    }else if(to.path === '/'){/*-->home*/
      next({path:'/home'})
    }else{
      if(storage.get("userInfo").role==2){/**/
        if (nextRoute1.indexOf(to.name) >= 0) {/*,-->home*/
            next({path:'/home'})
        }else{
          next()
        }
      }else{
        next()
      }
    }
  }else{/**/
    if (nextRoute.indexOf(to.name) >= 0){/*-->login*/
      next({path:'/'})
    }else{
      next()
    }
  }
});
export default router;



